import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { WelcomeVideoPage } from '../welcome-video/welcome-video';
import { NativeStorage } from '@ionic-native/native-storage';

@Component({
  selector: 'page-welcome-tutorial',
  templateUrl: 'welcome-tutorial.html',
})
export class WelcomeTutorialPage {

  slides = [
    {
      title: "Pequeno Ncleo!",
      description: "Nessa verso do aplicativo voc pode consultar todas os pequenos ncleos ativos na igreja e filtr-los por idade, nome e regio.",
      image: "assets/ministerios/acolhimento.jpg",
    },
    {
      title: "Testemunhos",
      description: "Agora todos os testemunhos que so enviados para o aplicativo passam por um filtro e depois so publicados em nosso site.",
      image: "assets/ministerios/acolhimento.jpg",
    },
    {
      title: "Pedido de Orao",
      description: "Os pedidos de orao que so enviados pelo aplicativo agora so automaticamente direcionados para a liderana da intercesso que alm de orar por voc, poder entrar em contato caso voc deseje.",
      image: "assets/ministerios/acolhimento.jpg",
    },
    {
      title: "Como Chegar",
      description: "Pelo menu lateral  possvel acessar ao mapa e telefone de todas as igrejas do Ncleo da F alm da chcara da igreja",
      image: "assets/ministerios/acolhimento.jpg",
    },
    {
      title: "Avisos",
      description: "Todos os avisos da igreja agora sero notificados no sino superior.",
      image: "assets/ministerios/acolhimento.jpg",
  }
  ];

  constructor(public navCtrl: NavController, public navParams: NavParams, public nativeStorage: NativeStorage) {
  }

  skip() {
    this.nativeStorage.setItem('welcome', {})
      .then(() => console.log('Stored item!'),
      error => console.error('Error storing item', error
      ));
    this.navCtrl.setRoot(WelcomeVideoPage);
  }
}



